import React from "react";
import Affair from "./Affair";
import {AffairType, DefaultAffairType, FilterType} from "./HW2";
import s from './Affairs.module.css'

type AffairsPropsType = { // need to fix any
    data: Array<DefaultAffairType>
    setFilter: (filter: FilterType) => void
    deleteAffairCallback: (id: number) => void
}

function Affairs(props: AffairsPropsType) {
    const mappedAffairs = props.data.map((a: AffairType) => (
        <Affair // should work
            key={a.id} // кеи ОБЯЗАТЕЛЬНЫ в 99% - так что лучше их писать всегда при создании компонент в мапе
            affair={a}
            deleteAffairCallback={props.deleteAffairCallback}
        />
    ))

    const setAll = () => {
        props.setFilter("all")
    }; // need to fix
    const setHigh = () => {
        props.setFilter("high")
    };
    const setMiddle = () => {
        props.setFilter("middle")
    };
    const setLow = () => {
        props.setFilter("low")
    };

    return (
        <div className={s.todoLists}>

            <div className={s.affairs}>
                {mappedAffairs}
            </div>
            <div className={s.statuses}>
                <button className={s.affair} onClick={setAll}>All</button>
                <button onClick={setHigh}>High</button>
                <button onClick={setMiddle}>Middle</button>
                <button onClick={setLow}>Low</button>
            </div>
        </div>
    );
}

export default Affairs;
